				success: function(e) {
					console.log(JSON.stringify(e), '---')
					uni.setStorageSync('systemInfo', e)
				}
			})


			let lan = 'en'
			let locale = uni.getStorageSync('locale')
			try {
				const res = uni.getSystemInfoSync()
